import React, { useEffect, useRef } from 'react';
import Title from '../Title/Title';
import styles from './Mission.module.scss';

import Image from '../../assets/img/mission/image.svg';

const Mission = ({ translationObj }) => {
  const missionRef = useRef(null);
  const imageRef = useRef(null);

  useEffect(() => {
    const missionContainer = missionRef.current;
    const missionImage = imageRef.current;

    const handleMouseMove = (e) => {
      const mouseX = e.clientX;
      const mouseY = e.clientY;

      const containerWidth = missionContainer.offsetWidth;
      const containerHeight = missionContainer.offsetHeight;

      // const imageWidth = missionImage.width;
      // const imageHeight = missionImage.height;

      const offsetX = (mouseX / containerWidth - 0.5) * 30;
      const offsetY = (mouseY / containerHeight - 0.5) * 30;

      missionImage.style.transform = `translate(${offsetX}px, ${offsetY}px)`;
    };

    const handleMouseLeave = () => {
      missionImage.style.transform = 'none';
    };

    missionContainer.addEventListener('mousemove', handleMouseMove);
    missionContainer.addEventListener('mouseleave', handleMouseLeave);

    return () => {
      missionContainer.removeEventListener('mousemove', handleMouseMove);
      missionContainer.removeEventListener('mouseleave', handleMouseLeave);
    };
  }, []);

  return (
    <section className={styles.mission} ref={missionRef}>
      <div className={styles.mission__container}>
        <div className={styles.mission__top}>
          <div className={styles.mission__title}>
            <Title title={translationObj.title} />
          </div>
          <div className={styles.mission__info}>
            <p className={styles.mission__suptitle}>{translationObj.firstSuptitle}</p>
            <p className={styles.mission__suptitle}>{translationObj.secondSuptitle}</p>
          </div>
        </div>
        <div className={styles.mission__wrapper}>
          <div className={styles.mission__image} ref={imageRef}>
            <img src={Image} alt="Фотография блока миссий" />
          </div>
          <div className={styles.mission__inner}>
            <h4 className={styles.mission__text}>{translationObj.text}</h4>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Mission;
